.accessibility-bar-wrapper {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.accessibility-bar-wrapper > .text {
  color: #000;
  font-family: sans-serif;
  font-size: 11px;
}

.accessibility-bar-wrapper > .list {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  list-style: none;
  margin: 0;
  padding: 10px 0;
}

.accessibility-bar-wrapper > .list > .item {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  padding: 0 10px;
}

.accessibility-bar-wrapper > .list > .item:not(:last-child) {
  border-right: 1px solid #000;
}

.accessibility-bar-wrapper > .list > .item > .action {
  background: none;
  border: none;
  color: #000;
  cursor: pointer;
  font-size: 15px;
  font-weight: 700;
  line-height: 1;
  margin: 0;
  padding: 0;
  transition: color 0.2s linear;
}

.accessibility-bar-wrapper > .list > .item > .action:hover {
  color: #555;
}

.accessibility-bar-wrapper > .list > .item > .link {
  display: inherit;
}

.accessibility-bar-wrapper > .list > .item .icon {
  display: inline-block;
  fill: #000;
  transition: fill 0.2s linear;
}

.accessibility-bar-wrapper > .list > .item .icon:hover {
  fill: #555;
}

.accessibility-bar-wrapper > .list > .item .action,
.accessibility-bar-wrapper > .list > .item .link,
.accessibility-bar-wrapper > .list > .item .icon {
  height: 0.9rem;
  width: 0.9rem;
}